<template>
  <div>

    <el-breadcrumb separator-class="el-icon-arrow-right" class="el-breadcrumb">
      <el-breadcrumb-item :to="{path:'/main'}">首页</el-breadcrumb-item>
      <el-breadcrumb-item>住户信息管理</el-breadcrumb-item>
      <el-breadcrumb-item>车位管理</el-breadcrumb-item>
    </el-breadcrumb>

    <el-card class="el-card">

      <el-row :gutter="10">
        <el-col :span="8">
          <el-input placeholder="请输入车位编号" v-model="carportNumber">
            <el-button slot="append" icon="el-icon-search" @click="handleSearch"></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <el-button type="primary" @click="addDialogVisible=true">添加车位</el-button>
        </el-col>
      </el-row>

      <el-table :data="tableData">
        <el-table-column prop="id" label="序号" width="70"></el-table-column>
        <el-table-column prop="carportNumber" label="车位编号" width="120"></el-table-column>
        <el-table-column prop="carportPlace" label="车位地址" width="170"></el-table-column>
        <el-table-column prop="carportTime" label="建造时间" width="120"></el-table-column>
        <el-table-column prop="carportState" label="车位归属" width="120"></el-table-column>
        <el-table-column label="操作" min-width="250">
          <template slot-scope="scope">
            <el-button type="primary" @click="showReadDialog(scope.row.id)" icon="el-icon-reading">查看</el-button>
            <el-button type="warning" @click="showEditDialog(scope.row.id)" icon="el-icon-edit">修改</el-button>
            <el-button type="danger" icon="el-icon-delete" @click="removeCarportById(scope.row.id)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <el-pagination
        background
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-size="pageSize"
        layout="total,prev, pager, next"
        :total="total">
      </el-pagination>
    </el-card>

    <el-dialog
      title="添加车位"
      :visible.sync="addDialogVisible"
      width="40%"
    >
      <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="90px" @close="addDialogClosed">
        <el-form-item label="车位编号" prop="carportNumber">
          <el-input v-model="addForm.carportNumber"></el-input>
        </el-form-item>
        <el-form-item label="车位地址" prop="carportPlace">
          <el-input v-model="addForm.carportPlace"></el-input>
        </el-form-item>
        <el-form-item label="建造时间" prop="carportTime">
          <el-input v-model="addForm.carportTime"></el-input>
        </el-form-item>
        <el-form-item label="车位归属" prop="carportState">
          <el-input v-model="addForm.carportState"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="addDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="addCarport">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="修改车位信息"
      :visible.sync="editDialogVisible"
      width="40%"
    >
      <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="90px" @close="editDialogClosed">
        <el-form-item label="车位编号" prop="carportNumber">
          <el-input v-model="editForm.carportNumber"></el-input>
        </el-form-item>
        <el-form-item label="车位地址" prop="carportPlace">
          <el-input v-model="editForm.carportPlace"></el-input>
        </el-form-item>
        <el-form-item label="建造时间" prop="carportTime">
          <el-input v-model="editForm.carportTime"></el-input>
        </el-form-item>
        <el-form-item label="车位归属" prop="carportState">
          <el-input v-model="editForm.carportState"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="editCarport">确 定</el-button>
      </span>
    </el-dialog>

    <el-dialog
      title="查看"
      :visible.sync="readDialogVisible"
      width="40%"
    >
      <el-form :model="readForm" ref="readFormRef" label-width="90px" >
        <el-form-item label="车位编号" prop="carportNumber">
          <el-input v-model="readForm.carportNumber" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="车位地址" prop="carportPlace">
          <el-input v-model="readForm.carportPlace" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="建造时间" prop="carportTime">
          <el-input v-model="readForm.carportTime" :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="车位归属" prop="carportState">
          <el-input v-model="readForm.carportState" :disabled="true"></el-input>
        </el-form-item>
      </el-form>

      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="readDialogVisible = false">关  闭</el-button>
      </span>
    </el-dialog>



  </div>
</template>

<script>
    export default {
        name: "carportManage",
      data() {
        return {
          pageSize: 8,
          currentPage: 1,
          tableData: {
            id:"",
            carportNumber:"",
            carportPlace:"",
            carportTime:"",
            carportState:""
          },
          total: 0,
          carportNumber: "",
          addDialogVisible: false,
          addForm:{
            id:"",
            carportNumber:"",
            carportPlace:"",
            carportTime:"",
            carportState:""
          },
          addFormRules:{
            carportNumber:[
              {required: true, message: '姓名不可为空', trigger: 'blur'},
              {min:1,max:20,message: '姓名长度在2~20个字符之间',trigger:'blur'}
            ],
            carportPlace:[
              {required: true, message: '性别不可为空', trigger: 'blur'}
            ],
            carportTime:[
              {required: true, message: '手机号不可为空', trigger: 'blur'}
            ],
            carportState:[
              {required: true, message: '地址不可为空', trigger: 'blur'}
            ]
          },
          editDialogVisible:false,
          editForm:{
            id:"",
            carportNumber:"",
            carportPlace:"",
            carportTime:"",
            carportState:""
          },
          editFormRules:{
            customerName:[
              {required: true, message: '姓名不可为空', trigger: 'blur'},
              {min:1,max:20,message: '姓名长度在2~20个字符之间',trigger:'blur'}
            ],
            customerSex:[
              {required: true, message: '性别不可为空', trigger: 'blur'}
            ],
            customerPhone:[
              {required: true, message: '手机号不可为空', trigger: 'blur'}
            ],
            customerAddress:[
              {required: true, message: '地址不可为空', trigger: 'blur'}
            ],
            customerTime:[
              {required: true, message: '入住时间不可为空', trigger: 'blur'}
            ]
          },
          readDialogVisible:false,
          readForm:{
            id:"",
            carportNumber:"",
            carportPlace:"",
            carportTime:"",
            carportState:""
          },
        }
      },
      created() {
        this.queryCarportList()
      },
      methods: {
        queryCarportList() {
          this.$http.get('carport/queryCarport',
            {params: {currentPage: this.currentPage, pageSize: this.pageSize, carportNumber: this.carportNumber}})
            .then(res => {
              this.total = res.data.total;
              this.tableData = res.data.data;
            });
        },
        handleCurrentChange(currentPage) {
          this.currentPage = currentPage
          this.queryCarportList()
        },
        handleSearch() {
          this.currentPage = 1
          this.queryCarportList()
        },
        addDialogClosed(){
          this.$refs.addFormRef.resetFields()
        },
        addCarport(){
          this.$refs.addFormRef.validate(async valid=>{
            if(!valid) return
            const {data:res}=await this.$http.post("carport/addCarport",this.addForm)
            if(res.code!==200){
              this.$message.error("添加房间信息失败,请检查房间信息")
            }else {
              this.$message.success("添加房间信息成功")
              this.addDialogVisible=false
              this.queryCarportList()
            }
          })
        },
        showEditDialog(id){
          this.editDialogVisible =true
          this.$http.get('carport/getCarportById',{params:{id:id}})
            .then(res => {
              this.editForm = res.data;
            });
        },
        editDialogClosed(){
          this.$refs.editFormRef.resetFields()
        },
        editCarport(){
          this.$refs.editFormRef.validate(async valid =>{
            if(!valid) return
            const {data:res}=await this.$http.post("carport/editCarport",this.editForm)
            if(res.code!==200){
              this.$message.error("修改房间信息失败,请检查房间信息")
            }else {
              this.$message.success("修改房间信息成功")
              this.editDialogVisible=false
              this.queryCarportList()
            }
          })
        },
        removeCarportById(id){
          this.$confirm('此操作将永久删除, 是否继续?', '提示', {
            confirmButtonText: '确定',
            cancelButtonText: '取消',
            type: 'warning'
          }).then(() => {
            this.$http.get('carport/removeCarportById',{params:{id:id}});
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.queryCarportList();
          }).catch(() => {
            this.$message({
              type: 'info',
              message: '已取消删除'
            });
          });
        },
        showReadDialog(id){
          this.readDialogVisible =true
          this.$http.get('carport/getCarportById',{params:{id:id}})
            .then(res => {
              this.readForm = res.data;
            });
        },
      }
    }
</script>

<style scoped lang="scss">
  .el-breadcrumb {
    margin-bottom: 15px;
    font-size: 18px;
  }

  .el-card {
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.15);
  }
</style>



